//*Увета для текстов и фонов
$color-main: #61b26a;
$color-white: #fff;

//*для фона
//в профиле
$bgc-gray: #f6f8f5;

//*цвета для текстов
//цвет заголовков, основной цвет текста, текст попапа с картинками
$color-black: #2c2e2c;
//цвет ссылок
$color-blue: #6195b2;
//цвет инпутов до заполнения, цвет заглавия таблички в профайл(фио, должность и т д)
$color-gray: #7d7e7c;
//error обводка инпута с ошибкой
$color-red: #bc3a3a;

//*mixin
//стили для заголовков форм, подтверждение email
@mixin title {
	color: $color-black;
	font-size: 32px;
	font-weight: 500;
	line-height: 44.8px; /* 140% */
}

//основной текст, текст ссылки, футера (без цвета)
@mixin text-size-16 {
	font-size: 16px;
	font-weight: 400;
	line-height: 20px;
}

// стили текста для активного и не активного инпута (без цвета)
@mixin text-size-14 {
	font-size: 14px;
	font-weight: 400;
	line-height: 20px;
}

//основные стили всех кнопок
@mixin button {
	@include text-size-16;
	border-radius: 12px;
	border: 1px solid #61b26a;
	padding: 12px 20px;
}
